import React,{useState} from 'react'

import './listitem.css'
import DeleteIcon from '@material-ui/icons/Delete';

function ToDo(props) {
    const [line,setLine]=useState(false)
    const LineFunction=()=>{
        setLine(true)
    }

    return (
        <div className="ToDoListDelete">
            <button onClick={LineFunction} className="deleteButton"><DeleteIcon/></button>
            <li style={{textDecoration:line ? "line-through":"none"}} className="list-Item">{props.text}</li>
           
        </div>
        )
   
}

export default ToDo
